<template>
  <div class="ProjectView">
      <div class="ProjectView__actions">
          <div class="ProjectView__input">
              <h2>{{ clientName }}</h2>
          </div>
          <div class="ProjectView__section">
              <Button @onClick="editContainer">Konteinera Akts</Button>
              <Button disabled>Darba Lapa</Button>
          </div>
          <div class="ProjectView__section">
              <Button @onClick="editPhotos">Iekraušanas foto</Button>
              <Button disabled>Specefikācija</Button>
          </div>
      </div>
      <div class="ProjectView__section ProjectView__section--finalize">
          <Button disabled>Saglabāt</Button>
          <Button
            @onClick="uploadToDropbox"
            :isLoading="isLoading"
          >
            Iesniegt
          </Button>
      </div>
  </div>
</template>

<script>
import Button from '../components/Button.vue';

export default {
    name: 'ProjectView',

    components: {
        Button
    },

    created() {
        if (this.$store.getters.container.length === 0) {
            this.$store.dispatch('createNewContainer')
        }
    },

    computed: {
        clientName() {
            return this.$store.getters.clientName;
        },
        isLoading() {
            return this.$store.getters.isLoading;
        }
    },

    methods: {
        editContainer() {
            this.$router.push('container');
        },
        editPhotos() {
            this.$router.push('photos');
        },
        uploadToDropbox() {
            this.$store.dispatch('uploadToDropbox');
        }
    }
}
</script>

