    height: 100%;
    display: flex;
    flex-direction: column;

    &__actions {
        flex: 1;
    }

    &__input {
        align-items: center;
        display: flex;
        flex-direction: column;
        padding: 20px 0;
    }

    &__section {
        display: flex;
        justify-content: space-around;
        padding: 20px 0;

        &--finalize {
            border: 1px solid black;
            padding: 10px 0;
            height: auto;
